import React, { useState } from "react";
import RaceTrack from"./components/races_track/racetrack.jsx";
// Datos de prueba, nombres y puntos de los caballos
const horses = [
  { name: "Caballo1", points: 10 },
  { name: "Caballo2", points: 8 },
  { name: "Caballo3", points: 6 },
  // Agrega ms caballos si lo deseas
];

// Funcin para simular la carrera y determinar el orden de llegada
function simulateRace(horses) {
  // Ordenar caballos por puntos (de mayor a menor)
  horses.sort((a, b) => b.points - a.points);

  // El resultado es un arreglo con los nombres de los caballos en orden de llegada
  const result = horses.map((horse) => horse.name);

  return result;
}

function RaceSimulator() {
  const [raceInProgress, setRaceInProgress] = useState(false);
  const [raceResult, setRaceResult] = useState([]);

  // Funcin para iniciar la carrera
  const startRace = () => {
    setRaceInProgress(true);

    setTimeout(() => {
      const result = simulateRace(horses);
      setRaceResult(result);
      setRaceInProgress(false);
    }, 3000); // Tiempo simulado de la carrera (3 segundos en este caso)
  };

  return (
    <div>
     
      {raceInProgress ? (
        <p>Carrera en progreso...</p>
      ) : (
        <>
         <RaceTrack/>
        </>
      )}
    </div>
  );
}

export default RaceSimulator;
