import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './encuestas.css';

const EncuestasModal = () => {
  const [showModal, setShowModal] = useState(true);
  const [surveyData, setSurveyData] = useState(null);

  const user = localStorage.getItem('user'); // Suponiendo que se almacena el usuario en localStorage

  useEffect(() => {
     setShowModal(true);
    const fetchData = async () => {
      try {
        const response = await axios.get(`/encuesta?user=${user}`);
        setSurveyData(response.data);
         setShowModal(true);

        // Verifica si el usuario no ha respondido la encuesta
        if (!response.data.answered) {
          setShowModal(true);
        }
      } catch (error) {
        console.error('Error fetching survey data:', error);
        setShowModal(true)
      }
    };

    fetchData();
  }, [user]);

  const handleCloseModal = () => {
    setShowModal(false);
    // Aqu podras guardar en el estado o en el localStorage que el usuario ya vio la encuesta
  };

  return (
    <>
      {showModal && (
        <div className="modal">
          <div className="modal-content">
            <span className="close" onClick={handleCloseModal}>&times;</span>
            <h2>Encuesta</h2>
            <p>Te gustara responder nuestra encuesta?</p>
            {/* Aqu iran los campos de la encuesta */}
          </div>
        </div>
      )}
    </>
  );
};

export default EncuestasModal;