import React, { useState, useEffect } from 'react';
import axios from 'axios';

function MarketBallo() {
  const [nftList, setNFTList] = useState([]);

  useEffect(() => {
    axios.get('https://api.hive-engine.com/rpc/contracts/nftmarket/list/BHREQT')
      .then((response) => {
        const nftList = response.data.result;
        setNFTList(nftList);
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  return (
    <div>
      <h1>NFT List</h1>
      <ul>
        {nftList.map((nft) => (
          <li key={nft._id}>
            <img src={nft.imageUrl} alt={nft.name} />
            <h2>{nft.name}</h2>
            <p>{nft.description}</p>
            <p>Price: {nft.price}</p>
          </li>
        ))}
      </ul>
    </div>
  );
}

export default MarketBallo;