import React, { useState, useEffect } from "react";
import axios from 'axios';
import KeychainForm from'./../../../hooks/useKeychainSubasta.jsx'; 
import './auctions.css'
import infoballo from'./../../../../assets/horseshoe-icon.svg'
import ModalBallo from"./components/infoballoSubastaModal.jsx"
import  "./../../../fetch/components/models/infoballoModal.css"
import Loader from"./../../../generals/loders/Loader.jsx"



let initialValue=false

function MyComponent(props) {
  const { sortedData } = props;
  const [isOpenInf, setIsOpenInf] = useState(initialValue);

  const openModalInf = () => setIsOpenInf(true);
  const closeModalInf = () => setIsOpenInf(false);

  const dataArr = Object.values(sortedData);

return (
  <div className='wrapp'>
    {dataArr.map((item, index) => {
      if (!item.itemId) {
        item.itemId = item.identidad;
      }
      if (!item.highestBidder) {
        item.highestBidder = "none";
      }
      if (!item._id) {
        item._id = null;
      }
      if (!item.currentPrice) {
        item.currentPrice = 1;
      }
        if (!item.imagen) {
        item.imagen = "";
      }

      let propsimo=[];propsimo.itemId=item.itemId;propsimo.Imagen=item.imagen;propsimo.name=item.itemId;propsimo.identidad=item.identidad;
       propsimo.genera=item.generacion;propsimo.salud=item.salud;propsimo.veloz=item.Velocidad;propsimo.addVeloz=0;propsimo.Resistencia=item.Resistencia;
       propsimo.addResi=0;propsimo.Agilidad=item.Agilidad;propsimo.sex=item.sexo;propsimo.ganadasTotal=0;propsimo.carrerasTotals=0;

      return (
        <div className='container' key={index}>
          <div className="card">
            <div className="box">
              <div className="content">
                <h2>{item.identidad}</h2>
                <div className="caja" style={{backgroundImage: `url("https://ipfs.io/ipfs/bafybeibydhcgj6ibcmdx2mo3vkktlo4chxuhyec3gqah3ojkxqvw5xh7ye")`}}>
                  <img src={item.imagen} alt="img"  />
                </div>
                <img src={infoballo} alt={item.itemId} className="iconomi" onClick={openModalInf} />
                <p>Bidder : {item.highestBidder}</p>
                <KeychainForm base_currency="BUDS" from={localStorage.getItem('user')} to="blockhorseracing" amount={item.currentPrice} memo={item.identidad} highestBidder={item.highestBidder} subasta_id={item._id}  status_last={item.status_lastBid}/>
                <p>Next Bid : {item.currentPrice} BUDS</p>
              </div>
            </div>
            <ModalBallo isOpen={isOpenInf} closeModal={closeModalInf} propsi={propsimo}>
              <button className="modal-close" onClick={closeModalInf}>
                X
              </button>
            </ModalBallo>
          </div>
        </div>
      );
    })}
  </div>
);
}


function App() {
  const [ballos, setBallos] = useState([]);
  const [subasta, setSubasta] = useState([]);
  const [sortedData, setSortedData] = useState([]);

  useEffect(() => {
// Obteniendo Horses del Blockchain    
    const fetchDataA=async () => {
      let url="https://engine.rishipanthee.com/contracts"; 
      let config = {
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*',
        },
      };
  
      let query={}
      query.account ="blockhorseracing";
    
      let body = JSON.stringify([
        {
          method: "find",
          jsonrpc: "2.0",
          params: {
            contract: 'nft',
            table: "BHRIGAinstances",
            query: query,
            limit: 1000,
            offset: 0,
            indexes: [],
          },
          id: 1,
        },
      ]);
  
      try {
        const response = await axios.post(url, body, config);
        const data = response.data[0].result;
  
        data.forEach((data, indexes) => {
          // ACA VAN TODAS LAS PROPIEDADES DE UN BALLO O GUA
  
          let Rarity=data.properties.Properties
          let identidad=data._id;
          let Generacion=data.properties.Attributes.substr(0,4)
          let Minteo=identidad + 1
          let Sexo=data.properties.Attributes.substr(5,1)
          let Salud=data.properties.Attributes.substr(7,3)
          let Velocidad=data.properties.Attributes.substr(11,3)
          let Resistencia=data.properties.Attributes.substr(15,3)
          let Agilidad=data.properties.Attributes.substr(19,3)
       
          let IPFS="https://ipfs.io/ipfs/" + data.properties.Details.substr(38,65);
  
          let Ballo = {
            id: Minteo,
            identidad:identidad,
            tipo: Rarity,
            generacion: Generacion,
            sexo: Sexo,
            salud: Salud,
           Velocidad: Velocidad,
           Resistencia: Resistencia,
           Agilidad: Agilidad,
            imagen: IPFS,
          };
  
          setBallos((Ballos) => [...Ballos, Ballo]);
        });
       
      } catch (error) {
        console.log("error fetching", error);
      } finally {
        //console.log("se ejecuta finally");
      }
    };
  

 


const fetchDataB = async () => {
    const responses = await axios.get('http://localhost:4000/auctions');
    const auctions = responses.data.result;
    console.log("Subastaasetear",auctions)
    setSubasta(auctions);
  };
    fetchDataA();
    fetchDataB();
  }, []);

  useEffect(() => {
    const ballosObj = ballos.map(item => ({
      identidad: item.identidad,
      ...item,
    }));

console.log("Ballos",ballosObj)


    const subastaObj = subasta.map(item => ({
      identidad: item.itemId,
      ...item,
    }));

 console.log("Subasta",subastaObj)



const objetoUnico = {};

const identidades = new Set([...ballosObj.map(item => item.identidad), ...subastaObj.map(item => item.identidad)]);

identidades.forEach(identidad => {
  const itemBallos = ballosObj.find(item => item.identidad === identidad);
  const itemSubasta = subastaObj.find(item => item.identidad === identidad);
  objetoUnico[identidad] = {
    ...itemBallos,
    ...itemSubasta
  };
});






// ballosObj.forEach(item => {
//   objetoUnico[item.identidad] = item;
// });

// subastaObj.forEach(item => {
//   objetoUnico[item.identidad] = item;
// });
console.log("Objeto nico", objetoUnico);

// if (ballosObj.identidad === subastaObj.itemId) {
  
//     let objetoFinal = {...ballosObj, ...subastaObj};
//     console.log(objetoFinal);
// }

//   const mergedData = {...ballosObj, ...subastaObj, identidad: ballosObj.identidad || subastaObj.identidad};

//   // setSortedData(objetoFinal);
// console.log("mergedData",mergedData);

// // const sortedData = [...ballosObj, ...subastaObj].sort((b, s) => b.identidad - s.itemId);
// //     console.log("sortedData",sortedData)
     setSortedData(objetoUnico);
  }, [ballos, subasta]);

  return (
    <div>
      <MyComponent sortedData={sortedData} />
    </div>
  );
}

export default App;
