import { useState } from "react";

export const useSubastaProcesador = (initialForm, validateForm) => {
  const [form, setForm] = useState(initialForm);
  const [errors, setErrors] = useState({});

  const handleChange = (e) => {
    const { name, value } = e.target;
    setForm({
      ...form,
      [name]: value
    });
    console.log("El form en el handle",form);
    console.log(form);
  };

  const handleBlur = (e) => {
    console.log("handleBlur")
    handleChange(e);
    setErrors(validateForm(form));
  };

  const handleSubmit = async (e) => {
    const { value } = e.target.name;
    console.log("VALUE",value)
    if (!Object.prototype.hasOwnProperty.call(window, "hive_keychain")) {
      alert("please install hive keychain");
    }
    window.hive_keychain.requestSignBuffer(
      value,
      "Login to Equine",
      "Posting",
      async (response) => {
        if (response.success === true) {
          localStorage.setItem("user", value);
          localStorage.setItem("auth", true);
          window.location.reload(false);
          return;
        } else {
          alert("error to confirm account");
          window.location.reload();
        }
      }
    );

    e.preventDefault();
  };

  return {
    form,
    errors,
    handleChange,
    handleBlur,
    handleSubmit
  };
};
