import React, { useState } from "react";
import Slider from "react-slick";
import "./slider.css";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import banner1 from "../../assets/banner/general/banner1.png";
import banner2 from "../../assets/banner/general/banner2.png";
import banner3 from "../../assets/banner/general/banner3.png";

export default function SimpleSlider() {
  const [currentSlide, setCurrentSlide] = useState(0);

  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true, // Establece el autoplay en true
    autoplaySpeed: 3000, // Establece el tiempo entre transiciones en 3000 milisegundos
  };

  return (
    <Slider
      {...settings}
      beforeChange={(oldIndex, newIndex) => setCurrentSlide(newIndex)}
      afterChange={(currentSlide) => setCurrentSlide(currentSlide)}
    >
      <div className="slide-img">
        <img src={banner1} alt="Imagen 5" className="slide-img" />
      </div>
      <div className="slide-img">
        <img src={banner2} alt="Imagen 5" className="slide-img" />
      </div>
      <div className="slide-img">
        <img src={banner3} alt="Imagen 5" className="slide-img"/>
      </div>
    
    </Slider>
  );
}
